.index-page{
  background-image: url(../img/bg.svg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: 0 0;
}

.main{
  position: relative;
  min-height: 56em;
  padding: 16.6875em 0 2em;
  text-align: center;
  &__float-btn{
    width: auto;
    position: absolute;
    padding: 0 2em;
    height: 3em;
    border-radius: 6.25em;
    z-index: 2;
    font-weight: 700;
  }
  ._btn1{
    left: 3.19em;
    top: 18.21em;
    transform: rotate(-11deg);
  }
  ._btn2{
    left: 48%;
    top: 9.46em;
    transform: translateX(-50%) rotate(-11deg);
  }
  ._btn3{
    right: 7.21em;
    top: 30.14em;
    transform: rotate(18deg);
  }
  &__arrow{
    position: absolute;
    &._view2{
      display: none;
    }
  }
  ._arrow1{
    width: 11.75em;
    left: 7.44em;
    top: 24.82em;
    transform: rotate(-167deg);
  }
  ._arrow2{
    width: 10.6875em;
    left: 47%;
    top: 16.96em;
    transform: translateX(-50%) rotate(-67deg);
  }
  ._arrow3{
    width: 16.375em;
    right: 12.38em;
    top: 31.25em;
  }
  &__content{
    z-index: 3;
    position: relative;
    top: 35%;
    left: 0;
    display: inline-block;
  }
  &__img{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__inner{
    max-width: 50em;
    width: 100%;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    font-size: 6.25em;
    line-height: 87%;
    text-transform: uppercase;
    font-weight: 900;
  }
  &__reward{
    padding: .75em 1.5em;
    margin-top: 2.1875em;
    border-radius: .75em;
    background: $linearText;
    font-weight: 700;
    span{
      font-size: 2em;
      line-height: 110%;
      letter-spacing: -0.01em;
    }
  }
  &__link{
    margin-top: 2.5em;
  }
}

.pre-launch{
  padding: 10em 0 5em;
  &__grid{
    display: grid;
    grid-template-columns: 35.3125em calc(100% - 35.3125em - .6875em);
    gap: .6875em;
    margin-top: 5em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
  }
}

.launch-process{
  background: $linearText;
  padding: 1.25em;
  border-radius: .75em;
  display: flex;
  flex-direction: column;
}

.launch-step{
  flex-grow: 1;
  padding: 1.25em 1.5em;
  border: 1px solid rgba(253, 250, 250, 0.2);
  border-radius: .5em;
  @include lastM(0 0 .6875em);
  display: flex;
  flex-direction: column;
  min-height: 11.5em;
  &--disabled{
    opacity: .5;
    pointer-events: none;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__num{
    white-space: nowrap;
    padding-top: .3125em;
  }
  &__desc{
    margin-top: .875em;
  }
  &__btn{
    margin-top: 1.5em;
    width: 100%;
    svg{
      width: 1.25em;
      margin-left: .5em;
    }
    &:hover{
      path{
        fill: #fff;
      }
    }
  }
  &__nav{
    margin-top: auto;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: .5em 1.25em;
    padding-top: 1.6875em;
  }
  &__social{
    width: 100%;
    svg{
      width: auto;
      height: 1em;
      margin-left: .5em;
    }
    &:hover{
      ._stroke{
        stroke: #fff;
      }
      ._fill{
        fill: #fff;
      }
    }
  }
}

.launch-code{
  margin-top: 1.25em;
  border: 1px dashed #fff;
  border-radius: .5em;
  background: rgba(255, 255, 255, 0.15);
  display: flex;
  align-items: center;
  height: 2.5em;
  padding-left: .75em;
  padding-right: 9em;
  position: relative;
  &__btn{
    position: absolute;
    top: -1px;
    right: -1px;
    width: 8.75em;
    height: calc(100% + 2px);
    padding: 0;
  }
  &__text{
    letter-spacing: -0.01em;
  }
}

.m-card{
  border-radius: .75em;
  background: radial-gradient(114.2% 133.85% at 7.14% 3.6%, rgb(35, 35, 54) 0%, rgb(35, 35, 54) 0.009999999747378752%, rgb(35, 35, 54) 100%);
  padding: 2em 1.25em 1.5em;
  position: relative;
  border: 1px solid rgba(#98A1C0,.07);
  &__line{
    position: absolute;
    top: 0;
    left: 0;
    background: radial-gradient(114.2% 133.85% at 7.14% 3.6%, rgb(35, 35, 54) 0%, rgb(35, 35, 54) 0.009999999747378752%, rgb(35, 35, 54) 100%);
    width: 100%;
    height: .75em;
    border-radius: .75em .75em 0 0;
  }
}

.advantage-card{
  padding-right: .25em;
  &._done{
    background: unset;
    .advantage-card__icon,
    .advantage-card__content{
      opacity: .15;
    }
    .m-card__line{
      opacity: 0;
    }
    &:after{
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      background-image: url(../img/done.svg);
      @include bgImgSize(8.3125em);
      left: 0;
      top: 0;
    }
  }
  &__icon{
    height: 2.75em;
    margin-bottom: 1.5em;
  }
  &__content{
    @include grid100gap(.25em);
  }
}


@media screen and (max-width: 1024px){
  .index-page{
    background-image: url(../img/bg3.svg);
  }

  .main{
    padding-top: 26.375em;
    min-height: 83em;
    ._btn1 {
      left: 1.19em;
      top: 42em;
    }
    ._btn2{
      top: 15em;
    }
    ._btn3{
      right: 5em;
      top: 50em;
    }
    ._arrow1{
      top: 48em;
      left: 5.5em;
    }
    ._arrow2{
      top: 22em;
    }
    ._arrow3{
      right: 10em;
      top: 51.5em;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__content{

    }
  }

  .pre-launch{
    padding-top: 12.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: .75em;
      margin-top: 2.5em;
    }
    &__items{
      grid-template-columns: repeat(4,calc(25% - 2.25em / 4));
      gap: .75em;
    }
  }

  .launch-process{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }

  .launch-step{
    margin: 0 !important;
  }

  .advantage-card{
    &__desc{
      .text12{
        white-space: nowrap;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .index-page{
    background-image: url(../img/page-bg-mob.svg);
  }

  .main{
    padding-top: 19em;
    padding-left: .75em;
    padding-right: .75em;
    //overflow: hidden;
    min-height: 50em;
    &__title{
      font-size: 3.5em;
      line-height: 110%;
    }
    &__reward{
      margin-top: .75em;
      span{
        font-size: 1.25em;
      }
    }
    &__link{
      margin-top: 3.5em;
      width: 100%;
      height: 3em;
    }
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__float-btn{
      padding: 0 1.5em;
      min-width: 6.8125em;
      span{
        font-size: .75em;
      }
    }
    &__arrow{
      position: absolute;
      z-index: 3;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    ._btn2{
      top: 10.43em;
      left: .47em;
      transform: rotate(-11deg);
    }
    ._arrow2{
      width: 5.3125em;
      top: 15.71em;
      left: 3em;
      transform: rotate(-98.5deg);
    }
    ._btn3{
      right: 2.5em;
      left: unset;
      top: unset;
      bottom: 2.5em;
      transform: rotate(-20.5deg);
      padding: 0 1.25em;
    }
    ._arrow3{
      width: 6em;
      top: 15.28em;
      right: 3.76em;
      transform: rotate(-40.5deg);
    }
    ._btn1{
      transform: rotate(18deg);
      left: unset;
      right: 0;
      top: 10.33em;
    }
    ._arrow1{
      transform: rotate(68deg);
      right: 5.625em;
      left: unset;
      top: unset;
      bottom: 7.5em;
      width: 5.25em;
    }
  }

  .pre-launch{
    padding-top: 11em;
    padding-bottom: 3.125em;
    &__inner{
      position: relative;
    }
    &__title{
      position: absolute;
      width: 100%;
      top: 2em;
      left: 0;
      .text36{
        font-size: 2em;
      }
      .linear-text{
        color: #fff;
        background: unset;
        -webkit-text-fill-color: unset;
      }
    }
    &__grid{
      gap: 1.25em;
      margin: 0;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .launch-process{
    grid-template-columns: 100%;
    gap: .75em;
    padding: 5.25em .75em 2.5em;
    width: calc(100% + 1.5em);
    margin-left: -.75em;
    border-radius: 0;
  }

  .launch-step{
    &__nav{
      grid-template-columns: 100%;
    }
  }
}
