.header{
  width: 100%;
  position: relative;
  z-index: 5;
  padding: 1.5em 0;
  @media screen and (min-width: 1025px){
    .container{
      padding: 0 1.25em;
    }
  }
  &--index{
    position: absolute;
    top: 0;
    left: 0;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 12.875em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__btn{
    padding: 0 1.0625em 0 .8125em;
    white-space: nowrap;
    img{
      width: 2em;
      height: auto;
      margin-right: .5em;
    }
    &._type2{
      border: 2px solid $primary;
      &:not(:hover){
        background: transparent;
      }
    }
  }
}

.menu .header__btn,
.menu__close,
.burger{
  display: none;
}

.nav{
  display: flex;
  align-items: center;
  &__item,
  &__link{
    @include lastM(0 1.5em 0 0);
  }
  &__link2,
  &__show,
  &__link{
    transition: all .3s;
    cursor: pointer;
    font-size: 1em;
    line-height: 120%;
    color: #98a1c0;
    display: flex;
    align-items: center;
    &:hover{
      color: #fff;
      ._stroke{
        stroke: #fff;
      }
      ._fill{
        fill: #fff;
      }
    }
    svg{
      width: 1.3125em;
      margin-right: .5em;
    }
  }
  &__show{
    &:after{
      content: '';
      flex-shrink: 0;
      width: .5em;
      height: .375em;
      background-image: url(../img/arrow.svg);
      @include bgImgSize(100%);
      margin-left: .5em;
    }
  }
  @media screen and (min-width: 577px){
    .hr{
      display: none;
    }
    &__item{
      position: relative;
      &:hover{
        .nav__hidden{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &__hidden{
      position: absolute;
      top: 100%;
      right: 1em;
      margin-top: -1px;
      padding-top: .75em;
      min-width: 11.75em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &__inner{
      background: #102039;
      border-radius: .5em;
      overflow: hidden;
    }
    &__link{
      &--bg{
        padding: 0 .75em;
        background: rgba(152, 161, 192, 0.1);
        border-radius: .5em;
        height: 2.1875em;
      }
    }
    &__link2{
      height: 3.1875em;
      padding: 0 1.25em;
      position: relative;
      &:hover{
        background: rgba(43, 58, 84, 0.4);
      }
      &:not(:last-child){
        position: relative;
        &:after{
          content: '';
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          width: calc(100% - 2.5em);
          height: 1px;
          background: rgba(152, 161, 192, 0.2);
        }
      }
    }
  }
}


@media screen and (max-width: 1024px){
  .header{
    &__logo{
      width: 2.3125em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: 1em 0;
    &__inner{
      justify-content: flex-end;
    }
    &__logo{
      margin-right: auto;
    }
    &__btn2{
      background: transparent;
      color: $textColor;
      border: 2px solid $primary;
      margin-right: .75em;
      path{
        stroke: #fff;
      }
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
        &__inner{
          transform: translateX(0);
        }
      }
    }
  }

  .burger{
    width: 2.5em;
    @include center;
    img{
      width: 100%;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    right: 0;
    height: $vh;
    width: 100%;
    z-index: 100;
    overflow: hidden;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background: rgba(17, 21, 36, 0.8);
    }
    &__inner{
      transition: all .3s;
      position: relative;
      z-index: 3;
      padding: .875em .625em;
      height: $vh;
      overflow-y: auto;
      width: 20.62em;
      margin-left: auto;
      background: $linearText;
      transform: translateX(150%);
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
    &__close{
      @include center;
      width: 2.5em;
      margin: 0 0 3.125em auto;
      img{
        width: 100%;
      }
    }
    .header__btn{
      margin: 3.75em 0 0 auto;
      display: flex;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-end;
    hr{
      width: 100%;
      height: 1px;
      background:  rgba(255, 255, 255, 0.2);
      border: none;
      margin-bottom: 1.5em;
      display: block;
    }
    &__show{
      display: none;
    }
    &__link2,
    &__link{
      font-size: 1.5em;
      justify-content: flex-end;
      color: #fff;
      ._stroke{
        stroke: #fff;
      }
      ._fill{
        fill: #fff;
      }
      svg{
        width: .83em;
        margin-right: .33em;
      }
    }
    &__link,
    &__link2,
    &__item{
      @include lastM(0 0 1em);
    }
    &__link2{
      &:before{
        content: '';
        flex-shrink: 0;
        width: .83em;
        height: .83em;
        background-image: url(../img/tg2.svg);
        @include bgImgSize(100%);
        margin-right: .33em;
      }
    }
  }
}
