.pre-launch2{
  padding: 5em 0 3em;
  &__inner{
    display: grid;
    grid-template-columns: 24.375em 35.375em;
    padding-left: 1.625em;
    justify-content: space-between;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__col2{
    padding-top: 3.375em;
  }
  &__desc{
    margin-top: 1.75em;
    @include grid100gap(1.75em);
  }
}

.benefits{
  padding: 5em 0 3.75em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - .8125em / 3));
    gap: .40625em;
    margin-top: 2.25em;
  }
}

.benefit-card{
  min-height: 14.375em;
  display: flex;
  align-items: flex-start;
  &._big{
    grid-column: 1/-1;
    min-height: 16.25em;
    align-items: center;
    overflow: hidden;
    .benefit-card__img{
      right: -1em;
    }
  }
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    &._view2{
      display: none;
    }
  }
  .m-card__line{
    z-index: 2;
  }
  &__content{
    position: relative;
    z-index: 2;
    @include grid100gap(.875em);
    max-width: 20.625em;
  }
}

.roadmap{
  clip-path: polygon( -0.208% 0%,100.278% 17.413%,100.278% 100%,-0.208% 82.587%,-0.208% 0% );
  background: $linearText;
  padding: 8.25em 0 12.5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.32em / 4));
    gap: .44em;
    margin-top: 3.31em;
  }
}

.roadmap-step{
  position: relative;
  &:not(:last-child){
    &:after{
      content: '';
      position: absolute;
      width: 4.0625em;
      height: 2em;
      background-image: url(../img/step-arrow.svg);
      @include bgImgSize(100%);
      top: 1.25em;
      left: 100%;
      margin-left: -4.5em;
    }
  }
  &:not(._active-arrow){
    &:after{
      opacity: .3;
    }
  }
  &__top{
    width: 11.875em;
    max-width: 100%;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    position: absolute;
    padding: 1.19em 0;
    text-align: center;
    width: 100%;
    top: 0;
    left: 0;
  }
  &__items{
    margin-top: 2.19em;
  }
  &:not(._current){
    .roadmap-step__top{
      img{
        opacity: .3;
      }
    }
  }
}

.conditions{
  padding: 1.5em 0 8em;
  &__inner{
    max-width: 59.625em;
    margin: 0 auto;
    padding: 4em 3.625em 3.125em 2.75em;
    display: grid;
    grid-template-columns: 29.5em 16em;
    justify-content: space-between;
  }
  &__desc{
    margin-top: 1.5em;
    @include grid100gap(1.5em);
    color: #98a1c0;
  }
  &__img{
    transform: rotate(13deg);
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 1024px){
  .pre-launch2{
    padding: 4.5em 0 5em;
    &__inner{
      grid-template-columns: 23em calc(100% - 28em);
      padding-left: 3.125em;
    }
    &__col2{
      padding-top: 3.5em;
    }
  }

  .benefits{
    padding-top: 6.25em;
  }

  .benefit-card{
    &__img{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
  }

  .roadmap{
    padding-top: 8.75em;
    clip-path: polygon( -22.363% 0%,118.945% 17.413%,118.945% 100%,-22.363% 82.587%,-22.363% 0% );
  }

  .conditions{
    padding: 12.5em 0 6.25em;
  }
}


@media screen and (max-width: 576px){
  .pre-launch2{
    padding: 3.125em 0;
    &__inner{
      grid-template-columns: 100%;
      padding: 0;
      gap: 2.25em;
    }
    &__col2{
      padding-top: 3.5em;
      order: -1;
    }
  }

  .benefits{
    padding: 3.125em 0 6.25em;
    &__grid{
      grid-template-columns: 100%;
      gap: .75em;
      margin-top: 1.5em;
    }
  }

  .benefit-card{
    min-height: 10em !important;
    &__img{
      &._view2{
        display: none;
      }
    }
  }

  .roadmap{
    background-size: cover;
    clip-path: polygon( -9.333% 0%,216.533% 7.363%,228.533% 100%,-12.8% 89.798%,-9.333% 0% );
    padding-bottom: 16.5em;
    .text36{
      text-align: center;
    }
    &__grid{
      margin-top: 2.81em;
      padding: 0 2em;
      gap: 5.69em;
      grid-template-columns: 100%;
    }
  }

  .roadmap-step{
    display: flex;
    flex-direction: column;
    align-items: center;
    &:not(:last-child){
      &:after{
        width: 4.0625em;
        height: 2em;
        background-image: url(../img/step-arrow.svg);
        top: 100%;
        left: 50%;
        transform: translateX(-50%) rotate(90deg);
        margin: 1em 0 0 0;
      }
    }
    &__top{
      margin-left: auto;
      margin-right: auto;
    }
  }

  .conditions{
    padding-top: 2em;
    padding-bottom: 12.5em;
    &__inner{
      padding: 2.75em 1.25em 2.5em;
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__img{
      width: 16.5em;
      max-width: 100%;
      margin-left: 2em;
    }
  }
}
