.footer{
  padding-top: 2em;
  padding-bottom: 2.75em;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 12.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__col2{
    display: flex;
  }
  &__c{
    margin-top: 2em;
  }
}

.footer-nav{
  margin-right: 10.875em;
  &__group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    color: $gray;
    &:not(:first-child){
      margin-top: .75em;
    }
    &:hover{
      color: #fff;
    }
  }
}

.footer-community{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__link{
    margin-top: .75em;
    display: flex;
    align-items: center;
    color: $gray;
    &:hover{
      color: #fff;
      path{
        fill: #fff;
      }
    }
    ._view2{
      display: none;
    }
  }
  &__icon{
    width: 1.75em;
    height: 1.5625em;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    margin-right: .56em;
    svg{
      max-width: 100%;
      max-height: 100%;
    }
  }
}



@media screen and (max-width: 1024px){
  .footer{
    padding-bottom: 7em;
  }

  .footer-nav{
    margin-right: 7.5em;
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-bottom: 5em;
    &__inner{
      display: block;
      position: relative;
      padding-bottom: 2.875em;
    }
    &__col2{
      margin-top: 1.75em;
      justify-content: space-between;
      padding-bottom: 1.75em;
      border-bottom: 1px solid rgba(#fff,.1);
    }
    &__c{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }
  }

  .footer-nav{
    margin-right: 0;
  }
}
