.referal{
  padding: 2.75em 0;
}

.referal-table{
  padding: 0 1em 1.5em;
  background: #111524;
  border-radius: .5em;
  margin-top: .75em;
  &__grid{
    display: grid;
    grid-template-columns: 2.25em calc(100% - 37.5em) 11.5em 23.75em;
    padding-right: 1.875em;
    align-items: center;
  }
  &__head{
    height: 3.25em;
    font-weight: 500;
    color: #98a1c0;
    ._view2{
      display: none;
    }
  }
}

.referal-table,
.referal-row{
  &__col{
    &:nth-child(3){
      text-align: center;
    }
    &:last-child{
      text-align: right;
    }
  }
}

.referal-row{
  border-top: 1px solid  rgba(152, 161, 192, 0.1);
  height: 3.625em;
  &__sum{
    span{
      color: rgba(152, 161, 192, 0.2);
    }
  }
  &__title{
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 1024px){
  .referal-table{
    &__grid{
      grid-template-columns: 2.25em calc(100% - 25.75em) 11.5em 12em;
      padding-right: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .referal{
    padding-top: .625em;
  }

  .referal-table{
    padding: 1em .75em;
    margin-top: 1em;
    &__grid{
      grid-template-columns: calc(100% - 12.25em) 8.25em 4em;
      padding-right: 0;
    }
    &__head{
      height: unset;
      padding-bottom: .5em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__col{
      &:nth-child(1){
        display: none;
      }
    }
  }

  .referal-row{
    position: relative;
    min-height: 2em;
    height: unset;
    &__col{
      &:nth-child(1){
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
      &:nth-child(2){
        padding-left: 1.25em;
      }
    }
    &__title{
      font-weight: 500;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
