  line-height: 110%;
}

.text32{
  font-size: 2em;
  line-height: 120%;
}

.text24{
  font-size: 1.5em;
  line-height: 120%;
}

.m-list span,
.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 120%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.linear-text{
  background: $linearText;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent
}

.text-center{
  text-align: center;
}

.gray-text{
  color: $gray;
}

.m-list{
  @include grid100gap(.25em);
  li{
    position: relative;
    padding-left: 1.66em;
    &:before{
      content: '';
      position: absolute;
      height: 100%;
      width: 1.66em;
      left: 0;
      top: 0;
      background-image: url(../img/list-arrow.svg);
      background-size: 1em auto;
      background-repeat: no-repeat;
      background-position: left center;
    }
  }
}


@media screen and (max-width: 992px){
  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
