.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-weight: 500;
    width: 11.5625em;
    height: 2.5em;
    border-radius: 0.5em;
    max-width: 100%;
    &:disabled{
        opacity: .5;
        cursor: not-allowed;
    }
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 119%;
        letter-spacing: -0.01em;
    }
}

.m-btn-blue-outline{
    color: $textColor;
    border: 2px solid $primary;
    &:hover{
        color: #FFF;
        background: $blue;
    }
}

.m-btn-white{
    color: $primary;
    background: #FFF;
    border: 1px solid #fff;
    &:hover{
        background-color: transparent;
        color: #fff;
    }
    &._type2{

    }
}

.m-btn-blue{
    color: #FFF;
    background: $blue;
    &:hover{
        color: #FFF;
        background: linear-gradient(91deg, #6892FE 23.13%, #6850FF 104.83%);
    }
}
