@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

html{
    height: auto;
    background: linear-gradient(180deg, #202738 0%, #070816 47.6%);
}

.m-page {
    color: #fff;
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    font-size: calc(1440px * (16 / 1440));
    @media screen and (max-width: 1440px){
        font-size: calc(100vw * (16 / 1440));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.index-page{
    @media screen and (min-width: 1025px){
        .container{
            padding: 0 9.5em;
        }
    }
}

.container {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding: 0 1.25em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 1024px){
    .m-page{
        font-size: calc(100vw * (16 / 1024));
    }

    .container{
        padding: 0 1.25em;
        &._mob-full{
            padding: 0;
        }
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 375));
    }

    .container{
        padding: 0 .75em;
    }
}


