.pagination{
  margin-top: 2em;
  @include center;
  &__title{
    margin: 0 1.19em;
  }
  &__btn{
    width: .75em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
    &:hover{
      opacity: .8;
    }
    &._disabled{
      opacity: .3;
      pointer-events: none;
    }
  }
}


@media screen and (max-width: 1024px){

}


@media screen and (max-width: 576px){
  .pagination{
    margin-top: 1em;
  }
}
